<template>
  <div class="login_page">
  <img src="../assets/1.png" class="bgc"/>
    <transition name="hello" appear>
      <div id="card">
        <el-form ref="loginForm" :model="form" :rules="rules" id="form-box" label-width="80px" style="border: 0">
          <div id="input-all">
            <el-form-item label="" prop="account">
              <el-input type="text" v-model="form.account" placeholder="" class="input-box"/>
            </el-form-item>

            <el-form-item label="" prop="password">
              <el-input type="password" v-model="form.password" placeholder="" @keyup.enter="enterSearchMember" class="input-box" show-password/>
            </el-form-item>
          </div>

          <el-form-item id="forget-remember">
            <el-checkbox v-model="checked"></el-checkbox>
            <el-button type="text" @click="addRoutes1" id="post"></el-button>
            <el-button type="text" @click="addRoutes2" id="forget"></el-button>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="onSubmit()" id="loading" class='submit'></el-button>
          </el-form-item>


          <el-form-item id="best-box">
            <el-button type="text" @click="addRoutes3"></el-button>
            <el-button type="text" style="margin-left: 100px" @click="addRoutes5"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </transition>
      <div id="police">
          <a href="https://beian.miit.gov.cn/" style="text-decoration: none">
<!--            -->
          </a>
          <br>
          <span>
<!--              ChromeFirefoxEdgeIE10360-->
          </span>
      </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Login",
  data() {
    return {
      // 
      form: {
        account: '',
        password: ''
      },
      user: "",
      number_dialog: 1,
      // el-form-item prop
      rules: {
        account: [
          {required: true, message: "", trigger: "blur"}
        ],
        password: [
          {required: true, message: "", trigger: "blur"}
        ]
      },
      checked: true,

        pingwei:['',
            ' Judges',
            'password',
            'root',
            '123456',
            '(30s)'],

        newDatas:[],
    }
  },
  methods: {

    onSubmit() {
      //
      const _this = this;

      if(this.form.account === ""){this.$message.warning("")}
      else if(this.form.password === ""){this.$message.warning("")}
      else{
          axios.post("api/students/signin", this.form).then(resp => {
            console.log(resp);//vue-router
            console.log(resp.data)
            this.user = this.form.account           //vue-router
            if (resp.data.ret === 0) {
              window.sessionStorage.setItem("user", this.user);
              // if(this.isMobile()){
              //     this.$router.push('phone_home');
              // }
              // else {
                  this.$router.push('index');
                this.$notify.closeAll();
              // }
              this.$message.success("");
              this.$cookies.set('user_name', this.user, '0');
              this.$cookies.set('number_dialog_school', this.number_dialog, '0', {path: '/Fillvolunteer'});
              if (this.checked === true) {
                this.setCookies(this.form.account, this.form.password, 7);
              } else {
                this.clearCookies();
              }
            }
            if (resp.data.ret !== 0) {
              this.$message.error("");
            }
          }).catch((err)=>{ console.log(err)})
      }
    },

    enterSearchMember() {
      this.onSubmit()  //
    },
    //
    open_notify() {
        const h = this.$createElement;
      for (let i in this.pingwei) {
        this.newDatas.push(h('p', null, this.pingwei[i]));
      }

      this.$notify({
          title: '',
          message: h('div', null, this.newDatas),
          duration: 30000,
      });
    },

    addRoutes1() {
      this.$router.push('/email');
      this.$notify.closeAll();
    },//
    addRoutes2() {
      this.$router.push('/forgetpassword');
        this.$notify.closeAll();
    },//
    addRoutes3() {
      this.$router.push('/login_root');
        this.$notify.closeAll();
    },//
    addRoutes5() {
      this.$router.push('Login_Teacher');
        this.$notify.closeAll();
    },//
    addRoutes4() {
      this.$router.push('/tealogin');
        this.$notify.closeAll();
    },//

    // Cookies
    setCookies(c_name, c_pwd, c_time) {
      var exdata = new Date();
      // 
      exdata.setTime(exdata.getDay() + 24 * 60 * 60 * 1000 * c_time);

      // Cookies
      var expare = exdata.toUTCString();
      window.document.cookie = 'userName' + '=' + c_name + ";" + expare + ";path=/";
      window.document.cookie = 'userPwd' + '=' + c_pwd + ";" + expare + ";path=/";
    },

    // Cookies
    getCookies(cname, cpwd) {
      var name = cname + "=";
      var pwd = cpwd + "=";
      // Cookies
      var decodedCookies = decodeURIComponent(document.cookie);

      // Cooiesca
      var ca = decodedCookies.split(';');

      for (let i = 0; i < ca.length; i++) {
        // ca
        var c = ca[i].split('=');
        if (c[0] == ' userName') {
          this.form.account = c[1];
        } else if (c[0] == ' userPwd') {
          this.form.password = c[1];
        }
      }
      return "";
    },

    // Cookies
    clearCookies: function () {
      this.setCookies("", "", -1);
    },

      // 
      isMobile(){
          let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);
          return flag;
      },
  },

  created() {
    axios({
      method:'GET',
      url:'api/students/flag',
    })
    this.open_notify();
    this.getCookies(this.form.account, this.form.password);
    const _this = this;
    document.onkeydown = function (e) {
      var key = window.event.keyCode;
      if (key == 13) {
        _this.enterSearchMember();
      }
    }
  },
}
</script>

<style scoped>
.login_page {
  width: 100%;
  height: 100%;
  background: url(../assets/2.png);
  background-size: cover;
  position: fixed;
  background-attachment: fixed !important;
}

.bgc{
  display: block;
  width: 45%;
  height: 100%;
}
#card {
  position: absolute;
  top: 45%;
  left: 75%;
  z-index: -999;
  transform: translate(-50%, -50%);
  width: 570px;
  height: 450px;
  //background-color: white;
  //box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
  //border-radius: 20px;
  background: url(../assets/zhigaokao_login.png);
  background-size: cover;
}

#login-title {
  margin: 0;
  padding-top: 40px;
  text-align: center;
}

#form-box {
  width: 450px;
  margin: 160px auto 0 auto;
  /*border: 1px solid blue;*/
}

.submit{
  margin-top: -5px;
  width: 180px;
  height: 30px;
  line-height: 8px;
}

.el-form-item {
  padding: 0;
}

#input-all {
  padding-left: 15px;
}

.input-box {
  width: 300px;
  margin-bottom: 10px;
}

.el-form >>> .el-form-item__error {
  padding-top: 0px !important;
}

#forget-remember {
  position: relative;
  margin-top: -15px;
  margin-bottom: 5px;
}

#post {
  position: absolute;
  left: 120px;
}

#forget {
  position: absolute;
  right: 90px;
}

#loading {
  width: 270px;
  margin-left: 10px;
  border-radius: 40px;
  margin-bottom: 5px;
}

#teacher {
  position: absolute;
  right: 80px;
}

#best-box {
  margin-top: -10px;
}

#police {
    position: fixed;
    bottom: 0;

    width: 100%;
    padding: 20px;
    margin: 0 auto;
    /*height: 200px;*/
    background-color: transparent;
    text-align: center;
    color: #333;
}

@media all and (max-width: 780px){
    #police {
        width: 780px;
        overflow-x: hidden;
    }
}

.hello-enter-active{
  animation: atguigu linear 1.5s forwards;
}


@keyframes atguigu {
  from{
    margin-left: -1000px;
  }
  to{

  }
}

</style>
