<template>
  <div>
    <el-card>
      <el-button type="primary" @click="dialogVisible = true"></el-button>
      <el-table :data="info.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe class="table_h">
        <el-table-column label="Consultant Information" align="center">
          <el-table-column prop="name" align="center" label="" width="220" >
          </el-table-column>
          <el-table-column prop="sex" align="center" label="" width="220" ></el-table-column>
          <el-table-column prop="age" align="center" label="" width="220" ></el-table-column>
          <el-table-column prop="phonenumber" align="center" label="" width="220"
                           autocomplete="off"></el-table-column>
          <el-table-column prop="major" align="center" label="" width="220"
                           autocomplete="off"></el-table-column>
          <el-table-column prop="mail" align="center" label="" ></el-table-column>
          <el-table-column align="center" label="" class="table-h">
            <template slot-scope="scope">
              <el-button type="danger " @click="deleted(scope.row.account)"></el-button>
            </template>
          </el-table-column>
        </el-table-column>
      </el-table>
      <el-dialog title="" :visible.sync="dialogVisible" width="28%">
        <el-form ref="add_form" :model="ruleForm" :rules="rules" label-width="100px">
          <el-form-item label="" prop="username">
            <el-input class="form" v-model="ruleForm.username" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" class="form" v-model="ruleForm.password" placeholder=""
                      show-password></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkPass">
            <el-input v-model="ruleForm.checkPass" class="form" placeholder="" show-password></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="ruleForm.name" class="form" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-input v-model="ruleForm.sex" class="form" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label="" prop="phonenumber">
            <el-input v-model="ruleForm.phonenumber" class="form" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="age">
            <el-input v-model="ruleForm.age" class="form" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label="" prop="major">
            <el-input v-model="ruleForm.major" class="form" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" prop="text">
            <el-input v-model="ruleForm.text" class="form" placeholder="" ></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="ruleForm.email" class="form" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="success" style="margin-left: 1px;margin-top: 10px" @click="onSubmit('add_form')">
            </el-button>
            <el-button type="info" style="margin-left: 50px" @click="resetForm('add_form')"></el-button>
            <el-button type="info" style="margin-left: 50px" @click="dialogVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <div>
        <el-pagination
            :page-size.sync="pageSize"
            :page-sizes="pageSizes"
            :current-page.sync="currentPage"
            :pager-count="pageCount"
            background
            layout="sizes,prev, pager, next,jumper,->,total"
            style="margin-top: 6px"
            :total=total
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from "axios"

export default {
  name: "Admain",
  data() {
    var validateRepassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.ruleForm.password) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      dialogVisible: false,
      info: [],
      total: 0,
      ruleForm: {
        username: '',
        password: '',
        email: '',
        checkPass: "",
        name:'',
        sex:'',
        phonenumber:'',
        age:'',
        major:'',
        text:''
      },
      pageSize: 15,  //
      pageSizes: [5, 10, 15, 20, 50],//
      currentPage: 1,  //
      pageCount: 7,   //
      rules: {
        username: [
          {required: true, message: "", trigger: "blur"}
        ],
        password: [
          {required: true, message: "", trigger: "blur"}
        ],
        email: [
          {required: true, message: "", trigger: "blur"},
          {
            type: 'string',
            message: '',
            trigger: 'blur',
            transform(value) {
              if (!/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/.test(value)) {
                return true
              }
            }
          },
        ],
        checkPass: [
          {required: true, validator: validateRepassword, trigger: "change"},
          {min: 6, max: 15, message: ' 6  15 ', trigger: 'change'}
        ],

      },
    }

  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    onSubmit() {
      // console.log(this.ruleForm)
      const _this = this;
      this.$refs['add_form'].validate((valid) => {
        if (valid) {
            axios({
              method:'POST',
              url:'api/manager/addteacher',
              data:{
                action:'add_teacher',
                account:window.sessionStorage.getItem('user'),
                set_mag:'manager',
                tch_account:this.ruleForm.username,
                tch_password:this.ruleForm.password,
                name:this.ruleForm.name,
                sex:this.ruleForm.sex,
                mail:this.ruleForm.email,
                phonenumber: this.ruleForm.phonenumber,
                age:this.ruleForm.age,
                major: this.ruleForm.major,
                major_set:'',
                text:this.ruleForm.text
              }
            }).then((resp)=>{
              console.log(resp.data)
              if (resp.data.ret ===0) {
                _this.$message.success("");
                this.get1();                //
                this.dialogVisible = false  //
              }else{
                _this.$message.error(resp.data.msg)
              }
            })

        } else {
          _this.$message.error("")
        }
      })
    },
    get1() {
      axios({
        method:'POST',
        url:'api/manager/getteacher',
        data:{
          action:'get_teacher',
          account:window.sessionStorage.getItem('user'),
          set_mag:'manager',
        }
      }).then((resp)=>{
        // console.log(resp.data)
        this.total = resp.data.length;
        this.info = resp.data.data;
        console.log(this.info)
      })
    },
    deleted(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).then(() => {
        axios({
          method:'DELETE',
          url:'api/manager/delteacher',
          data:{
            action:'del_teacher',
            account:window.sessionStorage.getItem('user'),
            set_mag:'manager',
            account_teacher:id
          }
        }).then((resp)=>{
          if (resp.data.ret ===0) {
            console.log(resp.data)
            this.$message.success("")
            this.get1();  //
          }
        })
      }).catch(() => {
        this.$message.success('');
      });
    },
  },
  created() {
    this.get1();
  }

}
</script>

<style scoped>
.form {
  /*width: 350px;*/
  width: 88%;
}
.el-card {
  margin-left: 20px;
  margin-top: 30px;
  /*width:1400px;*/
  /*height:800px;*/
}
.table_h{
  width: 100%;
  margin-top: 10px;
  font-size: 16px
}
</style>
