<template>
  <div class="login_page">
    <el-container>
      <el-main>
        <el-card class="box-card" >
          <div slot="header" >
            <h2 class="login-title"></h2>
          </div>
          <div>

            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="form">
              <el-form-item style="margin-left: 25px"  label=":" prop="username" align="left">
                <el-input v-model="ruleForm.username" placeholder=":" style="width: 250px"></el-input>
              </el-form-item>
              <el-form-item style="margin-left: 25px" label="  :" prop="email" align="left">
                <el-input v-model="ruleForm.email" placeholder=":" style="width: 250px"></el-input>
              </el-form-item>
              <el-form-item style="margin-left: 25px" label=":" prop="password" align="left">
                <el-input placeholder="" v-model="ruleForm.password" style="width: 250px" show-password></el-input>
              </el-form-item>
              <el-form-item style="margin-left: 25px" label=":" prop="changePass" align="left">
                <el-input placeholder="" v-model="ruleForm.changePass" style="width: 250px" show-password></el-input>
              </el-form-item>
              <el-form-item class="form" label="" prop="emailcode">
                <el-input v-model="ruleForm.emailcode" placeholder="" style="width: 50%"> </el-input>
                <el-button type="primary" round style="margin-left: 80px" @click="submitCode"></el-button>
              </el-form-item>
              <el-button type="primary" style="margin-left: 110px;margin-top: 8px" @click="getpass('ruleForm')" plain></el-button>
              <el-button type="cuccess" style="margin-left: 90px" @click="addRoutes1()"></el-button>

            </el-form>
          </div>

        </el-card>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ForgetPassword",
  data() {
    var validateRepassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.ruleForm.password) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        username: "",
        email: "",
        password: "",
        changePass:'',
        emailcode:''
      },
      info: {},
      rules: {
        email: [
          {required: true, message: '', trigger: 'blur'},
        ],
        username: [
          {required: true, message: '', trigger: 'blur'},
        ],
        password: [
          {required: true, message: '', trigger: 'blur'},
        ],
        changePass: [
          {required: true, validator: validateRepassword, trigger: "change"},
          {min: 6, max: 15, message: ' 6  15 ', trigger: 'change'}
        ],
      },
    }
  },
  methods: {
    getpass() {
      this.$refs['ruleForm'].validate((valid) => {
        if (valid) {
          console.log(this.ruleForm.username + '/' + this.ruleForm.email + '/' + this.ruleForm.password)
          axios({
            method:'POST',
            url:'api/students/findpassword',
            data:{
              account:this.ruleForm.username,
              password:this.ruleForm.password,
              mail:this.ruleForm.email,
              code:this.ruleForm.emailcode,
              session_id:this.ruleForm.session_id
            }
          }).then((resp)=>{
            if (resp.data.ret === 0) {
              this.$message.success("");
            }
            if (resp.data.ret !== 0) {
              this.$message.error(resp.data.msg);
              console.log(resp.data.msg)
            }
          })
        }
      })

    },
    submitCode(){
      axios({
        method:'POST',
        url:'api/students/givecode',
        data:{
          mail:this.ruleForm.email
        }
      }).then((resp)=>{
        console.log(resp.data)
        this.ruleForm.session_id=resp.data.session_id
      }).catch((err)=>{console.log(err,this.ruleForm.email)})
    },
    addRoutes1() {
      this.$router.push("/Login_Teacher")  //
    }
  }
}
</script>

