<template v-if="info">
    <div>
        <el-container>
            <el-main>
                <el-card class="box-card" >

                    <el-input v-model="data1" placeholder="" style="width:250px;"></el-input>
                    <el-button type="primary" @click="get()" style="margin: 5px auto;text-align: center" class="btn1"></el-button>
                    <el-button type="text" @click="dialogVisible = true" style="margin: 0 auto"> </el-button>
                    <br>
                    <br>
                    <div id="radio-all">
                        <div id="aside-box">
                            <div style="margin-left: -20px">
                                <el-button type="text"><h3>></h3></el-button>
                            </div>
                        </div>

                        <div id="button-box">
                            <!--/-->
                            <div>
                                <el-radio-group v-model="button_checked" size="small">
                                    <el-radio-button v-model="button_checked" label="ben"></el-radio-button>
                                    <el-radio-button v-model="button_checked" label="zhuan"></el-radio-button>
                                </el-radio-group>
                            </div>
                            <!--=-->
                            <div style="margin-top: 10px" v-if="choose_major(button_checked) == 1">
                                <el-select v-model="value" placeholder="">
                                    <el-option
                                        v-for="item in major_ben"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                                <el-button type="primary" class="btn1" @click="find_data"></el-button>
                            </div>
                            <!--=-->
                            <div style="margin-top: 10px" v-if="choose_major(button_checked) == 2">
                                <el-select v-model="value" placeholder="">
                                    <el-option
                                        v-for="item in major_zhuan"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                                <el-button type="primary" class="btn1" @click="find_data"></el-button>
                            </div>
                        </div>

                        <el-dialog title="" :visible.sync="dialogVisible" width="300px" style="text-align: left">
                            <h3 style="margin-top: -20px"> </h3>
                            <h3> </h3>
                            <h3> </h3>
                            <h3> </h3>
                            <h3> </h3>
                        </el-dialog>
                    </div>

                </el-card>
                <!---->
                <el-table
                    :data="info.slice((currentPage-1)*pageSize,currentPage*pageSize)"
                    stripe
                >
                    <el-table-column align="center" label="">
                        <el-table-column prop="name" align="center" label="" class="a" style="width: 100px">
                            <template slot-scope="scope">
                                <router-link :to="'bi'+'?id='+scope.row.id" class="a">
                                    <span>{{ scope.row.name }}</span>
                                </router-link>
                            </template>
                        </el-table-column>
                        <el-table-column prop="cc" align="center" label="" class="a" style="width: 100px">
                            <template slot-scope="scope">
                                <router-link :to="'bi'+'?id='+scope.row.id" class="a">
                                    <span v-if="scope.row.cc==1"></span>
                                    <span v-if="scope.row.cc==2"></span>
                                </router-link>
                            </template>
                        </el-table-column>
                        <el-table-column prop="major_code" align="center" label="" class="a">
                            <template slot-scope="scope">
                                <router-link :to="'bi'+'?id='+scope.row.id" class="a">
                                    <span>{{ scope.row.major_code }}</span>
                                </router-link>
                            </template>
                        </el-table-column>
                        <el-table-column prop="lv2_name" align="center" label="" class="a">
                            <template slot-scope="scope">
                                <router-link :to="'bi'+'?id='+scope.row.id" class="a">
                                    <span>{{ scope.row.lv2_name }}</span>
                                </router-link>
                            </template>
                        </el-table-column>
                    </el-table-column>
                    <el-table-column prop="major_year" align="center" label="" class="a">
                        <template slot-scope="scope">
                            <router-link :to="'bi'+'?id='+scope.row.id" class="a">
                                <span>{{ scope.row.major_year }}</span>
                            </router-link>
                        </template>
                    </el-table-column>

                    <el-table-column prop="degree" align="center" v-if="xuewei == true" label="" class="a">
                        <template slot-scope="scope">
                            <router-link :to="'bi'+'?id='+scope.row.id" class="a">
                                <span>{{ scope.row.degree }}</span>
                            </router-link>
                        </template>
                    </el-table-column>
                    <el-table-column prop="lv1_name" align="center" label="" class="a">
                        <template slot-scope="scope">
                            <router-link :to="'bi'+'?id='+scope.row.id" class="a">
                                <span>{{ scope.row.lv1_name }}</span>
                            </router-link>
                        </template>
                    </el-table-column>
                    <el-table-column prop="kaoyan" align="center" label="" class="a">
                        <template slot-scope="scope">
                            <router-link :to="'bi'+'?id='+scope.row.id" class="a">
                                <span>{{ scope.row.kaoyan }}</span>
                            </router-link>
                        </template>
                    </el-table-column>


                </el-table>
                <div>
                    <el-pagination
                        :page-size.sync="pageSize"
                        :page-sizes="pageSizes"
                        :current-page.sync="currentPage"
                        :pager-count="pageCount"
                        style="margin-top: 6px"
                        background
                        layout="prev, pager, next,jumper"
                        :total=total
                    >
                    </el-pagination>
                </div>
            </el-main>
        </el-container>

    </div>
</template>

<script>
export default {
    name: "phone_checkMajor",
    data() {
        return {
            num:'favor_no',
            button_checked: 'ben',
            xuewei:true,
            dialogVisible:false,

            // 
            major_ben:[
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
            ],
            major_zhuan:[
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
            ],
            value:'',

            data1: "",
            data2: "",
            info: [],

            total: 0,
            pageSize: 10,  //
            pageSizes: [5, 10, 15, 20, 50],//
            currentPage: 1,  //
            pageCount: 7,   //
        }
    },
    methods: {
        //
        get() {
            if(this.data1 == ''){this.$message.error("")}
            else{
                this.axios.get("http://101.34.248.53:8181/Map/major/byname", {params: {major_name: this.data1}})//
                    .then(resp => {
                        // console.log(resp)
                        this.total = resp.data.length;
                        this.info = resp.data;
                        this.$message.success("")
                    })
            }
        },

        //
        find_data() {
            if(this.value == ''){
                this.$message.error("")
            }else{
                if (this.button_checked == 'ben'){
                    this.axios.get("http://101.34.248.53:8181/Map/major/bycx/" + ''+ '%7C' + this.value)
                        .then(resp => {
                            this.$message.success("")
                            this.total = resp.data.length;
                            this.info = resp.data;
                            this.value='';
                        }).catch((error)=>{
                        console.log(error);
                    })
                }
                else if(this.button_checked == 'zhuan'){
                    this.axios.get("http://101.34.248.53:8181/Map/major/bycx/" + '' + '%7C' + this.value)
                        .then(resp => {
                            this.$message.success("")
                            this.total = resp.data.length;
                            this.info = resp.data;
                            this.xuewei = false;
                            this.value='';
                        }).catch((error)=>{
                            console.log(error);
                    })
                }

            }
        },

        //
        get3() {
            this.axios.get("http://101.34.248.53:8181/Map/major/byname", {params: {major_name: this.data2}})//
                .then(resp => {
                    // console.log(resp)
                    this.total = resp.data.length;
                    this.info = resp.data;
                })
        },

        choose_major(val) {
            if (val == 'ben')
                return 1;
            else {
                return 2;
            }
        },

        enterSearchMember() {
            this.get();
        }
    },
    created() {
        this.get3();
    }
}
</script>

<style scoped>
.el-main{
    margin: 0;
    padding: 0;
}

#radio-all{
    margin-top: -10px;
    position: relative;
}

#aside-box {
    width: 80px;
}

#button-box {
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: center;
    position: absolute;
    top: 20px;
    left: 0;
}

.a {
    text-decoration: none;
    font-size: 19px;
    color: #5e402f;
}
.a:hover{
    text-decoration: none;
    font-size: 19px;
    color: #ff5b00;
}

.box-card {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 260px;
    background-color: transparent;
    border: 0 solid transparent;
}

.el-table {
    width: 100vw;
    margin: 15px auto 0 auto;
}

.btn11{
    background-color: #0077ff;
}
</style>