<template>
    <div id="school_box">
        <el-container>
            <el-main>
                <el-card class="box-card" >
                    <el-input v-model="schoolName" style="width:250px;" placeholder=""></el-input>
                    <el-button type="primary" @click="get()" @keyup.enter="enterSearchMember"></el-button>
                    <el-button type="text" @click="dialogVisible = true" style="margin: 5px auto"> </el-button>
                    <div>
                        <el-container>
                            <el-aside width="80px">
                                <el-button type="text" style="margin-left: 0"><h3>></h3></el-button>
                                <el-button type="text" style="margin-left: 0;margin-top: -15px"><h3>></h3></el-button>
                                <el-button type="text" style="margin-left: 0;margin-top: -15px"><h3>></h3></el-button>
                                <el-button type="text" style="margin-left: 0;margin-top: -15px"><h3>></h3></el-button>
                            </el-aside>
                            <el-main>
                                <div id="check_box">
                                    <!---->
                                    <div>
                                        <el-select v-model="value_1" placeholder="">
                                            <el-option
                                                v-for="item in China_address"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </div>
                                    <!---->
                                    <div style="margin-top: 20px">
                                        <el-select v-model="value_2" placeholder="">
                                            <el-option
                                                v-for="item in School_type"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </div>
                                    <!---->
                                    <div style="margin-top: 20px">
                                        <el-select v-model="value_3" placeholder="">
                                            <el-option
                                                v-for="item in Class_type"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </div>
                                    <!---->
                                    <div style="margin-top: 20px">
                                        <el-select v-model="value_4" placeholder="">
                                            <el-option
                                                v-for="item in School_fun"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </div>
                                </div>
                                <el-button type="primary" @click="get2()" style="margin:5px auto;" class="btn1"></el-button>
                            </el-main>
                        </el-container>
                    </div>

                    <el-dialog title="" :visible.sync="dialogVisible" width="300px">
                        <h3 style="margin-top: -20px"> </h3>
                        <h3> </h3>
                        <h3> </h3>
                        <h3> </h3>
                        <h3> </h3>
                    </el-dialog>
                </el-card>
                <br>
                <br>
                <!--  -->
                <el-table border :data="info.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe>
                    <el-table-column align="center" label="">
                        <el-table-column align="center" label="" prop="school_name" class="table-h">
                            <template slot-scope="scope">
                                <router-link :to="'int'+'?id='+scope.row.id" >
                                    <img style="width: 50%" :src="petImage(scope.$index,scope.row)"/>
                                </router-link>
                            </template>
                        </el-table-column>

                        <el-table-column prop="school_name" align="center" label="" width="180" class="table-h">
                            <template slot-scope="scope">
                                <router-link :to="'int'+'?id='+scope.row.id" class="a">
                                    <span>{{ scope.row.school_name }}</span>
                                </router-link>
                            </template>
                        </el-table-column>
                    </el-table-column>
                    <el-table-column label="" align="center">
                        <el-table-column prop="school_type_cc" align="center" label="" class="table-h">
                            <template slot-scope="scope">
                                <router-link :to="'int'+'?id='+scope.row.id" class="a">
                                    <span>{{ scope.row.school_type_cc }}</span>
                                </router-link>
                            </template>
                        </el-table-column>

                        <el-table-column prop="school_bumen" align="center" label="" class="table-h">
                            <template slot-scope="scope">
                                <router-link :to="'int'+'?id='+scope.row.id" class="a">
                                    <span>{{ scope.row.school_bumen }}</span>
                                </router-link>
                            </template>
                        </el-table-column>

                        <el-table-column prop="school_chengshi" align="center" label="" class="table-h">
                            <template slot-scope="scope">
                                <router-link :to="'int'+'?id='+scope.row.id" class="a">
                                    <span>{{ scope.row.school_chengshi }}</span>
                                </router-link>
                            </template>
                        </el-table-column>

                        <el-table-column prop="school_email" align="center" label="" class="table-h">
                            <template slot-scope="scope">
                                <router-link :to="'int'+'?id='+scope.row.id" class="a">
                                    <span>{{ scope.row.school_email }}</span>
                                </router-link>
                            </template>
                        </el-table-column>

                        <el-table-column prop="school_zhandi" align="center" label="" class="table-h">
                            <template slot-scope="scope">
                                <router-link :to="'int'+'?id='+scope.row.id" class="a">
                                    <span>{{ scope.row.school_zhandi }}</span>
                                </router-link>
                            </template>
                        </el-table-column>
                    </el-table-column>
                </el-table>
                <div>
                    <el-pagination
                        :page-size.sync="pageSize"
                        :page-sizes="pageSizes"
                        :current-page.sync="currentPage"
                        :pager-count="pageCount"
                        background
                        style="margin: 15px 0 0 0"
                        layout="prev, pager, next"
                        :total=total
                    >
                    </el-pagination>
                </div>

            </el-main>
        </el-container>
    </div>
</template>

<script>
export default {
    name: "phone_checkSchool",
    data() {

        return {
            dialogVisible:false,

            schoolName: "",

            data3: "",//
            info: [],

            China_address:[
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
                {value:'',label:''},
            ],
            School_type:[
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
            ],
            Class_type:[
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '()',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
            ],
            School_fun:[
                {value: '',label: ''},
                {value: '985',label: '985'},
                {value: '211',label: '211'},
                {value: 'A',label: 'A'},
                {value: 'B',label: 'B'},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
                {value: '',label: ''},
            ],
            value_1:'',
            value_2:'',
            value_3:'',
            value_4:'',

            total: 0,
            pageSize: 10,  //
            pageSizes: [5, 10, 15, 20, 50],//
            currentPage: 1,  //
            pageCount: 7,   //


        }
    },
    //
    methods: {
        get() {
            if(this.schoolName == ''){this.$message.error("")}
            else{
                this.axios.get("http://101.34.248.53:8181/Map/list/byname", {params: {school_name: this.schoolName}})//
                    .then(resp => {
                        if (this.schoolName == '') {
                            this.$message.error('');
                        } else {
                            console.log(resp);
                            this.total = resp.data.length;
                            this.info = resp.data;
                            this.$message.success("");
                        }
                    })
            }
        },
        //
        get2() {
            if(this.value_1 == ''){this.$message.error("");}
            else if(this.value_2 == ''){this.$message.error("");}
            else if(this.value_3 == ''){this.$message.error("");}
            else if(this.value_4 == ''){this.$message.error("");}
            else{


                // console.log(this.value_1 + '/' + this.value_2 + '/' + this.value_3 + '/' + this.value_4)
                this.axios.get("http://101.34.248.53:8181/Map/list/bycx/" + this.value_1 + '/' + this.value_2 + '/' + this.value_3 + '/' + this.value_4)
                    .then(resp => {
                        this.info = resp.data;
                        this.total = resp.data.length;
                        console.log(resp.data)
                        this.$message.success("");
                    })
            }
            // }
        },
        //
        get3() {
            this.axios.get("http://101.34.248.53:8181/Map/list/byname", {params: {school_name: this.data3}})//
                .then(resp => {
                    console.log(resp);
                    this.total = resp.data.length;
                    this.info = resp.data;
                })
        },
        enterSearchMember() {
            this.get()
        },
        //
        petImage(index, row) {
            var url = "http://101.34.248.53:8181/Map/list/school_logo/" + row.school_name
            // console.log(url)
            return url
        },
    },

    //
    created() {
        this.get3();
        const _this = this;
        document.onkeydown = function() {
            var key = window.keyCode;
            if (key == 13) {
                _this.enterSearchMember();
            }
        }
    },
}
</script>

