<template>
  <div>
    <el-card class="el-card">
      <el-table :data="info.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe class="table_h">
        <el-table-column align="center" label="User Information">
          <el-table-column prop="account" align="center" label="" width="230"></el-table-column>
          <el-table-column prop="mail" align="center" label=""></el-table-column>
          <el-table-column prop="level" align="center" label="">
            <template>
              <span></span>
            </template>
          </el-table-column>

          <el-table-column align="center" label="">
            <template slot-scope="scope">
              <el-button type="danger " @click="deleted(scope.row.account)"></el-button>
            </template>
          </el-table-column>

        </el-table-column>
      </el-table>
      <!---->
      <div>
        <el-pagination
            :page-size.sync="pageSize"
            :page-sizes="pageSizes"
            :current-page.sync="currentPage"
            :pager-count="pageCount"
            background
            style="margin-top: 6px"
            layout="sizes,prev, pager, next,jumper,->,total"
            :total=total
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from "axios"

export default {
  name: "Admain",
  data() {
    var validateRepassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.ruleForm.password) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      dialogVisible: false,
      info: [],
      ruleForm: {
        username: '',
        password: '',
        email: '',
        time: "",
        checkPass: "",
        level: "",
      },
      total: 0,
      pageSize: 10,  //
      pageSizes: [5, 10, 15, 20, 50],//
      currentPage: 1,  //
      pageCount: 7,   //
      rules: {
        username: [
          {required: true, message: "", trigger: "blur"}
        ],
        password: [
          {required: true, message: "", trigger: "blur"}
        ],
        email: [
          {required: true, message: "", trigger: "blur"},
          {
            type: 'string',
            message: '',
            trigger: 'blur',
            transform(value) {
              if (!/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/.test(value)) {
                return true
              }
            }
          },
        ],
        checkPass: [
          {required: true, validator: validateRepassword, trigger: "change"},
          {min: 6, max: 15, message: ' 6  15 ', trigger: 'change'}
        ],

      },
    }
  },
  methods: {
    // 
    onSubmit() {
      // console.log(this.ruleForm)
      const _this = this;
      this.$refs['add_form'].validate((valid) => {
        if (valid) {
          axios.post('http://101.34.248.53:8181/Map/add', this.ruleForm).then(resp => {
            // console.log(this.ruleForm);
            // console.log(resp.data)

            if (resp.data == "Success") {
              _this.$message.success("");
              this.get1();                //
              this.dialogVisible = false  //
            }
            if (resp.data == "Repeat") {
              _this.$message.error("");
            }
            if (resp.data == "Repeat_Email") {
              _this.$message("");
            }
            if (resp.data == "Error") {
              _this.$message("");
            }

          })
        } else {
          _this.$message.error("")
        }
      })
    },

    get1() {
      axios({
        method:'POST',
        url:'api/manager/getuser ',
        data:{
          action:'get_user',
          account:window.sessionStorage.getItem('user'),
          set_mag:'manager'
        }
      }).then((resp)=>{
        console.log(resp.data.data)
        this.info = resp.data.data;
        this.total = resp.data.length;
      })
    },

    // 
    deleted(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).then(() => {
        axios({
          method:'DELETE',
          url:'api/manager/deluser',
          data:{
            action:'del_user',
            account:window.sessionStorage.getItem('user'),
            set_mag:'manager',
            account_user:id
          }
        }).then((resp)=>{
          if(resp.data.ret===0){
            this.$message.success("")
            this.get1();  //
          }
        })
      }).catch(()=>{this.$message('')})
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  created() {
    this.get1();
  }

}
</script>

<style scoped>
.form {
  /*width: 350px;*/
  width: 88%;
}

.font {
  font-size: 16px;
  font-style: italic;
  color: #fa9696;
  margin-left: 20px;
}

.el-card {
  margin-left: 20px;
  margin-top: 30px;
  /*width:1300px;*/
  /*height:800px;*/
}

.table_h {
  width: 100%;
  margin-top: 10px;
  font-size: 16px
}
</style>
