<script>
import axios from 'axios'
export default {

    name: "teacher",
    data() {
        return {
            activeName: 'first',
            radio_major: '',
            button_checked: 'ben',
            top_man: require("../assets/nan2.png"),
            top_woman: require("../assets/nv2.png"),
            teachers: [],
            dialogVisible: false,
            subjects: [],//
            text: "",//
            id:'',
            ruleForm: {},
            mained_square: "",//
            dialogVisible2:false,
            info: [],
            message:[],
            panduan:false
        }
    },
    methods: {
        // axios
        get_data() {
            axios({
              method:'POST',
              url:'api/students/getteacher',
              data:{
                action: 'get_teacher',
                account: this.$cookies.get('user_name')
              }
            }).then((resp)=>{
              console.log(resp.data.data[0])
              this.teachers=resp.data.data
            })
        },
        // 
        choose_major(val) {
            if (val == 'ben')
                return 1;
            else {
                return 2;
            }
        },
        //
        choose_teacher(major) {
            if (this.radio_major == '') {
                return true;
            } else if (major == this.radio_major) {
                return true;
            } else {
                return false;
            }
        },
        // 
        teacher_introduce(index) {
            this.$notify({
                title: '',
                message: this.teachers[index].text,
                duration: 10000,
            })
        },

        //
        onSubmit() {
          this.main_square = this.$cookies.get('user_name');
          if(this.text == ''){this.$message.error("")}
            else {
            axios({
              method:'POST',
              url:'api/students/givequestion',
              data:{
                action:'give_question',
                // account:'12345678',
                account:this.$cookies.get('user_name'),
                question:this.text,
                tch_code:this.mained_square
              }
            }).then((resp)=>{
              console.log(resp.data)
              if (resp.data.ret ===0) {
                this.$message.success("")
                this.get_data()
                this.dialogVisible = false
                this.new_get()
                this.mywenti()
              }
            })


            }
        },

        //
        zixun(index) {
            this.dialogVisible = true;
            //
            this.mained_square = this.teachers[index].account;
            // this.file_post(index);
        },
        new_get(){
            this.text = ""
        },
        //
        mywenti() {
          axios({
            method:'POST',
            url:" api/students/getquestion",
            data:{
              action:'get_question',
              // account:'12345678'
              account:this.$cookies.get('user_name'),
            }
          }).then((resp)=>{
            console.log(resp.data.question)
            this.info=resp.data.question
          })
        }
    },
    created() {
        this.get_data();
        this.mywenti()
    }
}
</script>

