<template>
    <div id="all">
        <div id="box-1">
            <div class="set"></div>
            <!--                                        -->
            <el-select v-model="rank_value" placeholder="">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
            <!--                                        -->
            <div>
                <el-table :data="table_rank" style="width: 100%" stripe>
                    <el-table-column prop="year" label=""></el-table-column>
                    <el-table-column prop="cc" label=""></el-table-column>
                    <el-table-column prop="school_min_score" label="" :formatter="formatSchool"></el-table-column>
                    <el-table-column prop="school_min_rank" label="" :formatter="formatSchool"></el-table-column>
                </el-table>
            </div>
        </div>

        <div id="box-2">
            <div class="set"></div>
            <!--                                        -->
            <el-select v-model="sel_value" placeholder="">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
            <!--                                        -->
            <div id="plan">
                <el-table :data="table_major.slice((curPage-1)*pageSize,curPage*pageSize)" style="width: 100%"
                          stripe>
                    <el-table-column prop="major" label="" width="250"></el-table-column>
                    <el-table-column prop="menlei" label=""></el-table-column>
                    <el-table-column prop="plan" label=""></el-table-column>
                    <el-table-column prop="xuezhi" label=""></el-table-column>
                    <el-table-column prop="xvanke" label=""></el-table-column>
                    <el-table-column prop="lowest_score" label=""></el-table-column>
                    <el-table-column prop="lowest_ranking" label=""></el-table-column>
                </el-table>
                <!--                                        -->
                <el-pagination
                    :page-size.sync='pageSize'
                    :current-page.sync='curPage'
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    layout="prev, pager, next"
                    :total="tab_length">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "schoolTable",
    data() {
        return {
            //  ------------------------------
            // 
            rank_value: '',
            // 
            sel_value: '',

            options: [
                {
                    value: '2021',
                    label: '2021',
                },
                {
                    value: '2022',
                    label: '2022',
                }
            ],

            //  ------------------------------------
            pageSize: 10,
            curPage: 1,
            tab_length: 0,

            // ----------------------------------------
            // 
            text_arr:[],
            // 
            major_arr:[],

            table_rank:[],
            table_major:[],
        }
    },
    created() {
        this.getAllData().then(()=>{
            let date = new Date();
            let year = date.getFullYear();
            this.sel_value = year;
            this.rank_value = year;
        })
    },
    methods: {
        // 
        async getAllData(){
            let cid = this.$route.query.id;

            // 
            await this.axios({
                method:'get',
                url:`http://101.34.248.53:8181/Map/list/Major_score/Byid/${cid}`
            }).then(({data}) => {

                // console.log(data,'');

                // 
                for (const item of data) {
                    if(item.year === 2020) {
                        item.year = 2021;
                    } else {
                        item.year = 2022;
                    }
                }

                // 
                this.major_arr = data;
                this.tab_length = data.length;
            })

            // 
            await this.axios({
                method:'get',
                url:`http://101.34.248.53:8181/Map/list/school_score_rank/Byid/${cid}`
            }).then(({data})=>{

                // console.log(data,'');

                for (const item of data) {
                    if(item.year == 2020) {
                        item.year = 2021;
                    } else {
                        item.year = 2022;
                    }
                }

                this.text_arr = data;
            })
        },

        handleSizeChange(val) {
            this.pageSize = val;
        },

        handleCurrentChange(val) {
            this.curPage = val;
        },

        // 
        yearChooseMajor(year) {
            let arr = [];
            let major = this.major_arr;

            for (const item of major) {
                if(item.year == year) {
                    arr.push(item);
                }
            }

            this.table_major = arr;
        },

        // 
        yearChooseRank(year) {
            let arr = [];
            let rank = this.text_arr;

            for (const item of rank) {
                if(item.year == year) {
                    arr.push(item);
                }
            }

            this.table_rank = arr;
        },

        // ---------------------------------------------
        formatSchool(row, column) {
            let num = row[column.property];
            if (num < 0) {
                return '';
            } else {
                return num;
            }
        },
    },
    watch:{
        'rank_value':function () {
            // console.log('',this.rank_value);
            this.yearChooseRank(this.rank_value);
        },

        'sel_value':function () {
            // console.log('',this.sel_value);
            this.yearChooseMajor(this.sel_value);
        }
    }
}
</script>

<style scoped>
#all {
    box-sizing: border-box;
    margin: 0;
    position: relative;
    max-height: 1500px;
}

.set {
    margin: 5px 0;
    padding: 5px;
    border-style: solid;
    border-width: 0 0 0 3px;
    border-color: brown;
}

#box-1, #box-2 {
    margin: 0;
    padding: 5px;
    box-shadow: 0px 6px 12px 3px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    position: relative;
}

#box-2 {
    margin-top: 50px;
    box-shadow: 0px 6px 12px 3px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
}

.el-select {
    position: absolute;
    top: 0;
    right: 20px;
}
</style>
