<template>
  <div class="login_page">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <h2 class="login-title"></h2>
      </div>

      <div>
        <div class="user_logon_develop_ym">
          <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px">

            <el-form-item label="   : " prop="name">
              <el-input type="text" v-model="ruleForm.name" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="   : " prop="name">
              <el-input type="text" v-model="ruleForm.username" placeholder="" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="   : " prop="name">
              <el-input type="text" v-model="ruleForm.password" placeholder="" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="   : " prop="email">
              <el-input type="text" v-model="ruleForm.email" placeholder=""></el-input>
            </el-form-item>

            <el-form-item label="  : " prop="age">
              <el-input type="text" v-model="ruleForm.age" placeholder="" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="  : " prop="tel">
              <el-input type="text" v-model="ruleForm.tel" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <div>
              <el-button type="text" style="margin-top: -10px;margin-left: 54px" class="text"></el-button>
              <el-radio-group v-model="ruleForm.sex" size="medium">
                <el-radio-button label=""></el-radio-button>
                <el-radio-button label=""></el-radio-button>
              </el-radio-group>
            </div>
            <el-button type="text" style="margin-top: 15px;margin-left: 26px" class="text"></el-button>
            <el-cascader
                style="margin-left: 0px"
                placeholder=""
                :options="options1"
                v-model="ruleForm.major"
                clearable
                collapse-tags
                :props="{ checkStrictly: true ,expandTrigger: 'hover' }"
            >
            </el-cascader>
            <div>
              <el-button type="text" style="margin-top: 15px;margin-left: 26px" class="text"></el-button>
              <el-input
                  type="textarea"
                  :rows="4"
                  placeholder=""
                  v-model="ruleForm.info"
                  maxlength="100"
                  class="form"
                  show-word-limit>
              </el-input>
            </div>
            <el-form-item>
              <el-button type="primary" style="margin-left: 20px;margin-top: 30px" @click="addform()"></el-button>
<!--              <el-button style="margin-left: 30px" @click="resetForm('ruleForm')"></el-button>-->
              <el-button style="margin-left: 30px" @click="cost_log()"></el-button>
              <el-button style="margin-left: 30px" @click="addtest()"></el-button>
            </el-form-item>
          </el-form>
        </div>

      </div>
    </el-card>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "LogonDevelop",
  data() {

    const name = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('!'));
      }
    };
    const age = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('!'));
      }
    };
    // const tel = (rule, value, callback) => {
    //   if (!value) {
    //     return callback(new Error('!'));
    //   }
    // };
    const tel = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('!'));
      } else if (value && (!(/^[1][345789]\d{9}$/).test(value) || !(/^[1-9]\d*$/).test(value) || value.length !== 11)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const email = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }, 100)
    };
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.ruleForm.password !== '') {
          this.$refs.ruleForm.validateField('password');
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.ruleForm.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        info: "",
        name: '',
        email: '',
        age: '',
        major: "",
        tel: '',
        key: "",
        sex: "",
        username:"",
        password: "",
        checkPass: "",
      },
      rules: {
        name: [
          {validator: name, trigger: 'blur'}
        ],
        age: [
          {validator: age, trigger: 'blur'}
        ],
        tel: [
          {validator: tel, trigger: 'blur'}
        ],
        email: [
          {validator: email, trigger: 'blur'}
        ],
        password: [
          {validator: validatePass, trigger: 'blur'}
        ],
        checkPass: [
          {validator: validatePass2, trigger: 'blur'}
        ],
      },
      options1: [
        {
          value: '', label: '',
          children: [
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
          ]
        },
        {
          value: '', label: '',
          children: [
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
            {value: '', label: '',},
          ]
        }

      ],
    };
  },
  methods: {
    addform() {
      if (this.ruleForm.name != '' && this.ruleForm.email != '' && this.ruleForm.info != '' && this.ruleForm.age != ''
          && this.ruleForm.sex != '' && this.ruleForm.tel != ''&& this.ruleForm.password != '' && this.ruleForm.major != '') {
        console.log(this.ruleForm);
        axios.get('http://101.34.248.53:8181//Map/teacher/add/' + this.ruleForm.name + '/' + this.ruleForm.username+ '/' + this.ruleForm.password+ '/' + this.ruleForm.sex + '/'
            + this.ruleForm.age + '/' + this.ruleForm.tel + '/' + this.ruleForm.major + '/' + this.ruleForm.email + '/' + this.ruleForm.info).then(resp => {
          // axios.post('http://101.34.248.53:8181//Map/teacher/add',this.ruleForm).then(resp=>{
          console.log(resp.data);
          if (resp.data == "OK") {this.$message.success("");}
          if (resp.data == "Repeat_Username") {this.$message.error("");}
          if (resp.data == "Repeat_Email") {this.$message.error("");}

        })
      } else {
        this.$message.error("")
      }
    },
    addtest() {
      this.$router.push('/')
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    cost_log(){
      this.ruleForm.name = ""
      this.ruleForm.email = ""
      this.ruleForm.info = ""
      this.ruleForm.age = ""
      this.ruleForm.sex = ""
      this.ruleForm.tel = ""
      this.ruleForm.major =""
    },
    get22() {
      this.ruleForm.major = "";
    }
  }
}
</script>
<style scoped>
.login_page {
  width: 100%;
  height:100%;
  background: url(https://pic.imgdb.cn/item/613a140244eaada73973b202.jpg) ;
  background-size: cover;
  background-attachment: fixed!important;
  position: fixed;
  overflow-y: scroll;
}

.user_logon_develop_ym {
  width: 500px;
  margin: auto;
}

.box-card {
  border-radius: 40px;
  margin: auto;
  /*margin-left: 580px;*/
  margin-top: 100px;
  width: 950px;
  height: 800px;
}

.text {
  color: #000000;
}
.text:hover{
  color: #000000;
}

.login-title {
  text-align: center;
  margin: 0 auto 10px auto;
  margin-top: 10px;
  color: #303133;
}

.form {
  width: 400px;
  margin-top: 20px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
</style>

