<template>
  <div id="tab-box">
    <!---->
    <div id="mark-box" v-if="(show == 0)?false:true">
      <span>{{ mark }}</span>
      <el-divider direction="vertical"></el-divider>
      <span>{{ rank }}</span>
      <el-divider direction="vertical"></el-divider>
      <span>
                
                <span v-for="(item,index) in sub" :key="index" class="sub-box">{{ item }}</span>
            </span>
      <el-divider direction="vertical"></el-divider>
      <!--            -->
      <el-button @click="showDialog = true" title="" style="border: 0">
        <i class="el-icon-edit"></i>
      </el-button>
    </div>

    <el-tabs v-model="activeName" @tab-click="handleTabClick" >
      <el-tab-pane v-for="(item,index) in fun" :label="item.name" :key="index" :name="item.name">
        <router-view :name="item.cnm" v-if="item.show"></router-view>
      </el-tab-pane>
    </el-tabs>

    <!--dialog-->
    <el-dialog
        :visible.sync="showDialog"
        title=""
        width="30vw"
        :before-close="Close_dialog"
        id="dialog-box"
    >
      <span class="dia-tit"></span>
      <el-checkbox-group v-model="check_data" id="check-box" style="margin-top: 15px" :max="3">
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
      </el-checkbox-group>
      <br>
      <br>
      <span class="dia-tit"></span>
      <br>
      <el-input v-model="data_fenshu" placeholder="100-750" max="750" min="100" style="margin-top: 15px"></el-input>
      <br>
      <br>
      <span class="dia-tit"></span>
      <br>
      <el-input v-model="data_paiming" placeholder="" style="margin-top: 15px"></el-input>
      <div id="dialog_button">
        <el-button type="success" @click="Con_dialog" ></el-button>
        <el-button type="warning" @click="Close_dialog"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import  axios from 'axios'
export default {
  name: "Major_tab",
  data(){
    return{
      num:0,
      user_name:'',
      activeName:'',
      fun:[
        {
          name:'',
          cnm:'fil',
          show:true,
        },
        {
          name:'',
          cnm:'fom',
          show: false,
        },
      ],

      // dialogdialog
      showDialog:false,
      check_data:[],
      data_fenshu:null,
      data_paiming:null,


      // 
      show:0,
      mark:null,
      rank:null,
      sub:[],
    }
  },
  methods:{
    // 
    Con_dialog(){
      if(this.data_fenshu == null || this.data_paiming == null){
        this.$message.warning(',');
      }
      else if(this.data_fenshu < 100 || this.data_fenshu > 750){
        this.$message.warning('');
      }
      else if(this.check_data.length == 0){
        this.$message.warning('');
      }else if(this.check_data.length<3)
        this.$message.warning((""))
      else {
        this.data_Axios();
      }
    },

    //  /  dialog 
    Close_dialog(){
      this.$confirm(null,{
        title:'',
        message:'',
        confirmButtonText:'',
        cancelButtonText:'',
      }).then(()=>{
        this.Con_dialog();
      }).catch(()=>{
        if(this.show == 0){
          this.$message.error("");
        }else {
          this.$message.info('');
          this.showDialog = false;
        }
      })
    },

    // 
    data_Axios(){
      axios({
        method:'POST',
        url:'api/students/grades',
        data:{
          action:'add_stu',
          stu_grd:parseInt(this.data_fenshu),
          address:'',
          subject:this.check_data,
          id:'',
          ranking:parseInt(this.data_paiming),
          account:this.$cookies.get('user_name'),
        }
      }).then((resp)=>{
          console.log(resp.data)
        if(resp.data.ret ===0){
          this.$message.success('');
          this.showDialog = false;
          // 
          this.get_Axios();
        }
        else{
          this.$message.error(resp.data.msg);
        }
      }).catch((err)=>{
        console.log(err)})
    },

    // 
    get_Axios(){
        axios({
          method:'GET',
          url:'api/students/grades',
          params:{
            action:'get_stu',
            account:this.$cookies.get('user_name')
            // account:'12345678'
          }
        }).then((resp)=>{
          console.log('@@@@',resp.data)
          if(resp.data.ret ===0){
            this.show = 1;
            this.rank = resp.data.ranking;
            this.mark = resp.data.stu_grd;
            this.sub = resp.data.subject;
          }
        })
    },


    // 
    handleTabClick(tab){
      let name = tab.name;
      switch (name){
        case '':
        {
          this.fun[0].show = true;
          this.fun[1].show = false;
        }break;
        case '':
        {
          this.fun[0].show = false;
          this.fun[1].show = true;
        }
      }
    },
  },
  mounted(){
    console.log(111)
    axios({
      method:'GET',
      url:'api/students/checkdata',
      params:{
        account:this.$cookies.get('user_name')
      }
    }).then((resp)=>{
      if(resp.data.ret===0){
        this.showDialog=false
        this.get_Axios()
        this.show=1
      }else{
        this.$message.error(resp.data.msg)
        this.showDialog=true
      }
    })
  }
}
</script>

<style scoped>
#tab-box{
  margin: 0 10px;
  padding: 0;
}

#mark-box{
  background-color: white;
  width: 35vw;
  /*border: 1px solid blue;*/
  border-radius: 25px;
  padding: 5px;
  text-align: center;
  margin: 0 0 0 auto;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1);
}

.sub-box{
  display: inline-block;
  width: 70px;
  height: 30px;
  border: 1px solid rgba(255, 85, 143, 0.35);
  border-radius: 15px;
  text-align: center;
  line-height: 30px;
  margin: 0 5px;
}

.sub-box:hover{
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1);
}

/deep/.el-tabs__item:focus.is-active.is-focus:not(:active) {
  box-shadow: none;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

/deep/.el-dialog__title{
  font-size: 24px;
  font-weight: 700;
  margin-top: 15px;
}

#dialog-box{
  text-align: center;
  padding: 10px;
}

.dia-tit{
  color: #333;
  font-size: 20px;
  font-weight: 700;
}

#check-box{
  width: 300px;
  text-align: left;
  margin: 0 auto;
  padding-left: 50px;
}

.el-dialog__body .el-input{
  width: 15vw!important;
}

#dialog_button{
  margin-top: 20px;
}
</style>