<template>
  <div id="day">
    <div id="tuchu">
    </div>
    <div id="top-box">
      <div class="block">
        <el-date-picker
            v-model="value_day"
            type="month"
            placeholder=""
            value-format="yyyy-M"
            @change="op_day(value_day)"
            default-value=value_day>
        </el-date-picker>
      </div>
    </div>
    <div id="numer_tit">
       / 
    </div>

    <div id="time_tit">
      
    </div>
    <div id="user"></div>
  </div>
</template>

<script>
export default {
  name: "tableinfo",
  data() {
    return {
      arr: null,
      value_day: '2021-9',
      now_year: 2021,
      now_month: 9,
      // arr:require('@/data/com.json')
    }
  },
  created() {
      let date = new Date();
      this.now_year = date.getFullYear();
      this.now_month = date.getMonth() + 1;
      this.value_day = `${this.now_year}-${this.now_month}`;
    this.get_data();
  },
  methods: {
    op_day(val) {
      var date = new Date()
      const Regex_1 = /^[0-9]{1,4}/gm;
      const Regex_2 = /[0-9]{1,2}$/gm;
      this.now_year = Regex_1.exec(val);
      this.now_month = Regex_2.exec(val);

      this.get_data();
    },

    get_data() {
      this.axios.get('http://101.34.248.53:8181/Map/PV_record/' + this.now_year + '/' + this.now_month)
          .then(resp => {
            this.arr = resp.data;
            if(this.arr.length == 1){
              return alert("!!!");
            }

            this.run(this.arr);
          }).catch(error => {
        console.log(error)
      })
    },

    run(_rawData) {
      let myChart = this.$echarts.init(document.getElementById('user'));
      // 
      var countries = ['', '', ''];
      // 
      var datasetWithFilters = [];
      // 
      var seriesList = [];
      // 
      this.$echarts.util.each(countries, function (country) {
        // id
        var datasetId = 'dataset_' + country;
        datasetWithFilters.push({
          id: datasetId,
          // id
          fromDatasetId: 'dataset_raw',
          // 
          transform: {
            // 
            type: 'filter',
            config: {
              // 
              and: [
                // 
                {dimension: 'Time', gte: 1},
                {dimension: 'Name', '=': country}
              ]
            }
          }
        });

        // 
        seriesList.push({
          type: 'line',
          datasetId: datasetId,
          // true
          showSymbol: false,
          name: country,
          // 
          endLabel: {
            show: true,
            formatter: function (params) {
              return params.value[2] + ': ' + params.value[0];
            }
          },
          // 
          labelLayout: {
            moveOverlap: 'shiftY'
          },
          // 
          emphasis: {
            focus: 'series'
          },
          // 
          encode: {
            x: 'Time',
            y: 'Sum',
            // 
            label: ['Name', 'Sum'],
            // 
            itemName: 'Time',
            // tooltip
            tooltip: ['Sum'],
          }
        });
      });

      myChart.setOption({
        animationDuration: 10000,
        dataset: [{
          id: 'dataset_raw',
          source: _rawData
        }].concat(datasetWithFilters),
        // 
        // title: {
        //     text: 'PV',
        //     textStyle:{
        //         fontSize:14,
        //         color:'#FFF',
        //     }
        // },
        // 
        tooltip: {
          order: 'valueDesc',
          trigger: 'axis',
        },
        xAxis: {
          type: 'category',
          nameLocation: 'middle'
        },
        yAxis: {
          name: 'Sum'
        },
        // 
        grid: {
          top: 100,
          right: 140
        },

        // 
        series: seriesList
      })
    },
  },
}
</script>

<style scoped>
/**/
#day {
  background-color: #ffffff;
  width: 1000px;
  margin: 0 auto;
  /*border:1px solid red;*/
  border-radius: 25px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  text-align: center;
  position: relative;
}

#top-box {
  width: 600px;
  margin: 0 auto;
}

/**/
#tuchu {
  font-style: italic;
  height: 50px;
  font-size: 16px;
  color: #fa9696;
  line-height: 50px;
}

#numer_tit{
  width: 60px;
  height: 25px;
  color: black;
  font-size: 14px;
  background-color: white;
  position: absolute;
  top: 150px;
  left: 55px;
  z-index: 9999;
}

#time_tit{
  width: 60px;
  height: 25px;
  color: black;
  font-size: 14px;
  background-color: white;
  position: absolute;
  bottom: 45px;
  right: 70px;
  z-index: 9999;
}
/**/
#user {
  width: 1000px;
  height: 700px;
}
</style>
