                </el-form>
                <el-form :inline="true" :model="GKweici" class="demo-form-inline">
                    <el-form-item label="">
                        <el-input v-model="GKweici" placeholder=""></el-input>
                    </el-form-item>
                </el-form>

                <div id="tool-box">
                    <el-tooltip content=",">
                        <i class="el-icon-question"/>
                    </el-tooltip>
                </div>
            </div>

            <!-- -->
            <div class="block">
                <div>
                    <el-button type="text" style="margin-top: -15px"><h3></h3></el-button>
                    <el-radio-group v-model="button_checked" size="small">
                        <el-radio-button style="margin-left: 5px" v-model="button_checked" label="ben"></el-radio-button>
                        <el-radio-button v-model="button_checked" label="zhuan"></el-radio-button>
                    </el-radio-group>
                    <br>
                    <!-- -->
                    <el-button type="text" style="margin-top: 5px;"><h3></h3></el-button>
                    <el-cascader
                        class="width_form"
                        placeholder=""
                        :options="options2"
                        filterable
                        v-model="list2"
                        clearable
                        collapse-tags
                        :props="{expandTrigger: 'hover' }"
                        v-show="panduan == true"
                    >
                    </el-cascader>

                    <el-cascader
                        class="width_form"
                        placeholder=""
                        :options="options2"
                        v-model="list2"
                        clearable
                        collapse-tags
                        :props="{expandTrigger: 'hover' }"
                        v-show="panduan == false"
                    >

                    </el-cascader>
                    <!-- -->
                    <el-button type="text" style="margin-top: 5px;"><h3></h3></el-button>
                    <el-cascader
                        class="width_form"
                        placeholder=""
                        :options="options4"
                        filterable
                        v-model="list3"
                        clearable
                        collapse-tags
                        :props="{expandTrigger: 'hover' }"
                    >
                    </el-cascader>

                    <!---->
                    <el-button type="text" style="margin-top: 5px;"><h3></h3></el-button>
                    <el-switch
