                        @click="get1()">
                        
                    </el-button>
                </div>

            </div>


        </el-card>
        <!---->
        <el-card class="box-card2">
            <el-table :data="info.slice((currentPage-1)*pageSize,currentPage*pageSize)" border
                      style="margin-top: 0;" :row-style="{height:'70px'}" stripe>
                <el-table-column align="center" label="2021">
                    <el-table-column prop="major" align="center" label="" width="180" class="tableBox">
                        <template slot-scope="scope">
                            <span class="a">{{ scope.row.major }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="university" align="center" label="">
                        <template slot-scope="scope">
                            <router-link :to="'int'+'?id='+scope.row.school_id" class="a">
