                    <template slot-scope="scope">
                        <span class="a" v-if="scope.row.cc==1"></span>
                        <span class="a" v-if="scope.row.cc==0"></span>
                    </template>
                </el-table-column>
                <el-table-column prop="rank_min" align="center" label="">
                    <template slot-scope="scope">
                        <span class="a">{{ scope.row.rank_min }}</span>
                    </template>
                </el-table-column>
