<script>
export default {
    name: "phone_MajorSpring",
    data(){
        return {
            button_checked: 'ben',
            dialogVisible: false,
            value: "true",//
            value1: "true",//
            panduan: true, ///
            add: "",
            GKfenshu: "", //
            GKweici: "", //
            GKfenshu1: "500", //
            GKweici1: "20", //
            //
            list2: "",
            list3: "*",
            list22: "",
            list33: "",
            info: [],
            total: 0,
            pageSize: 10,  //
            pageSizes: [5, 10, 15, 20, 50],//
            currentPage: 1,  //
            pageCount: 7,   //
            //
            options2: [
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
                {value: '', label: '',},
            ],
            //
            options4: [
                {value: '', label: ''},
                {value: '', label: ''},
            ],
        }
    },
    methods:{
        get1() {
            // console.log(+ '' +this.GKfenshu + '/' + this.GKweici + '/' + this.list2 + '/' + this.list3 + '/' + this.value)
            // if (this.choose_major() == 1) {
            if (this.GKfenshu == '') {
                this.$message.warning("")
            } else if (this.GKweici == '') {
                this.$message.warning("")
            } else if (this.list2 == '') {
                this.$message.warning("")
            } else {
                if (this.list3 == '') this.list3 = '*';
                console.log('' + this.GKfenshu + '/' + this.GKweici + '/' + this.list2 + '/' + this.list3 + '/' + this.value)
                this.axios.get("http://101.34.248.53:8181/Map/spring_list/zyzhcx/" + '' + '/' + this.GKfenshu + '/' + this.GKweici + '/' + this.list2 + '/' + this.list3 + '/' + this.value)
                    .then(resp => {
                        console.log(resp);
                        this.total = resp.data.length;
                        this.info = resp.data;
                    })
            }
            // }
        },
        get3(){
            //
            // if (this.choose_major() == 2) {
            if (this.GKfenshu == '') {
                this.$message.warning("")
            } else if (this.GKweici == '') {
                this.$message.warning("")
            } else if (this.list2 == '') {
                this.$message.warning("")
            } else {
                console.log('' + '/' + this.GKfenshu + '/' + this.GKweici + '/' + this.list2 + '/' + this.list3 + '/' + this.value)
                this.axios.get("http://101.34.248.53:8181/Map/spring_list/zyzhcx/" + "" + '/' + this.GKfenshu + '/' + this.GKweici + '/' + this.list2 + '/' + this.list3 + '/' + this.value)
                    .then(resp => {
                        console.log(resp);
                        this.total = resp.data.length;
                        this.info = resp.data;
                    })
            }
            // }
        },
        //
        get2() {
            this.axios.get("http://101.34.248.53:8181/Map/spring_list/zyzhcx/" + '' + '/' + this.GKfenshu1 + '/' + this.GKweici1 + '/' + this.list22 + '/' + this.list33 + '/' + this.value1).then(resp => {
                console.log(resp);
                this.total = resp.data.length;
                this.info = resp.data;
            })
        },
        choose_major(val) {
            if (val == 'ben')
                return 1;
            else {
                return 2;
            }
        },
        panduan1() {
            this.panduan = true;
            this.add = "";
            console.log(this.panduan)
            this.$message.success("")
        },
        panduan2() {
            this.panduan = false;
            this.add = "";
            this.$message.success("")
        },
    },
    created() {
        this.get2();
        // console.log(this.num_dialog);
    },
}
</script>

