    get1() {
      if (this.list1 == '') this.list1 = '*';
      if (this.list2 == '') this.list2 = '*';
      if (this.list3 == '') this.list3 = '*';
      if (this.list4 == '') this.list4 = '';
      axios.get("http://101.34.248.53:8181/Map/list/zyzhcx/" + '' + '/' + this.username +'/' + this.list1 + '/' + this.list2 + '/' + this.list3 + '/' + this.list4 + '/' + this.value)
          .then(resp => {
            // console.log(resp);
            this.info = resp.data;
            this.total = resp.data.length;
          })
    },

    // 
    get2() {
      if (this.list1 == '') this.list1 = '*';
      if (this.list2 == '') this.list2 = '*';
      if (this.list3 == '') this.list3 = '*';
      if (this.list4 == '') this.list4 = '';
      axios.get("http://101.34.248.53:8181/Map/list/zyzhcx/" + "" + '/' + this.username +'/' + this.list1 + '/' + this.list2 + '/' + this.list3 + '/' + this.list4 + '/' + this.value)
          .then(resp => {
            console.log(resp);
            this.info = resp.data;
            this.total = resp.data.length;
          })
    },

    //
    getSchool() {
      axios.get("http://101.34.248.53:8181/Map/list/zyzhcx/" + "" + "/" + this.username + '/' + this.list11 + '/' + this.list22 + '/' + this.list33 + '/' + this.list44 + '/' + this.num2)
          .then(resp => {
            this.info = resp.data;
            this.total = resp.data.length;
          })
    },

    choose_major(val) {
      if (val == 'ben')
        return 1;
      else {
        return 2;
      }
    },

    // 
    get_Axios(){
      let get_data;
      this.axios('http://101.34.248.53:8181/Map/Voluntary/Voluntary_Info_if/' + this.username,{
        method:'get'
      }).then((resp)=>{
        get_data = resp.data;
        this.show = get_data.State;
        this.rank = get_data.Rank;
        this.mark = get_data.Mark;
        this.sub = get_data.Subjects.split(',');

        // dialog
        if(this.show == 1){
          this.showDialog = false;
        }else {
          this.showDialog = true;
        }
      });
    },
  },

