<template>
  <el-container>
    <el-header style="height:20vh; padding: 0;">
      <el-card class="box-card" shadow="hover">
        <div>
          <div id="radio_all">
            <!--  -->
            <div id="aside-box">
              <div style="margin-top: -15px">
                <el-button type="text" style="margin-top: -8px"><h3>></h3></el-button>
                <br>
                <el-button type="text" style="margin-top: -22px"><h3>></h3></el-button>
              </div>
            </div>

            <div id="button-box">
              <!--/-->
              <div style="margin-top: -27px">
                <el-radio-group v-model="button_checked" size="small">
                    <el-radio-button label="all"></el-radio-button>
                    <el-radio-button label="ben"></el-radio-button>
                    <el-radio-button label="zhuan"></el-radio-button>
                </el-radio-group>
              </div>

                <!---->
                <div style="margin-top: 22px" v-if="choose_major(button_checked) == 0">
                    <el-select placeholder="">
                        <el-option></el-option>
                    </el-select>
                </div>
              <!---->
              <div style="margin-top: 22px" v-if="choose_major(button_checked) == 1">
                  <el-select v-model="value" placeholder="">
                      <el-option
                          v-for="item in major_ben"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                      </el-option>
                  </el-select>
              </div>
              <!--=-->
              <div style="margin-top: 22px" v-if="choose_major(button_checked) == 2">
                  <el-select v-model="value" placeholder="">
                      <el-option
                          v-for="item in major_zhuan"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                      </el-option>
                  </el-select>
              </div>
            </div>
          </div>
        </div>
      </el-card>
    </el-header>
    <el-main>
        <!---->
      <div id="th-box">
        <template v-for="(num,index) in teachers">
          <div
              class="box"
              v-show="choose_teacher(num.major)"
              :key="index">
            <img :src="num.sex == ''?top_man:top_woman">
            <div class="teacher-intro">
              <p>{{ num.name }}</p>
              <p id="question">{{ num.num }}</p>
              <p>{{ num.age }}</p>
              <p>{{ num.sex }}</p>
              <p>{{ num.major }}</p>
              <el-button type="primary" plain @click="teacher_introduce(index)"></el-button>
              <el-button type="success" plain @click="zixun(index)" class="zixun_button"></el-button>
            </div>
          </div>
        </template>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "teacher",
  data() {
    return {
        button_checked: 'all',
        top_man: require("../assets/nan2.png"),
        top_woman: require("../assets/nv2.png"),
        major_ben:[
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
        ],
        major_zhuan:[
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
            {value:'',label:''},
        ],
        teachers: [],
        value: '',
    }
  },
  methods: {
    // axios
    get_data() {
      this.axios.get('http://101.34.248.53:8181/Map/list_teacher')
          .then(resp => {
            this.teachers = resp.data;
          });
    },

    // 
    choose_major(val) {
      if (val == 'all')
          return 0;
      else if (val == 'ben')
          return 1;
      else
          return 2;
    },

    //
    choose_teacher(major) {
      if (this.button_checked == 'all')
        return true;

      else if (major == this.value) {
        return true;
      }

      else {
        return false;
      }
    },

    // 
    teacher_introduce(index) {
      this.$notify({
        title: '',
        message: this.teachers[index].info,
        duration: 10000,
      })
    },

    //
    file_post(index) {
      let num = this.teachers[index].id;
      this.axios({
        url: 'http://101.34.248.53:8181/Map/teacher',
        method: 'post',
        params: {
          'id': num
        },
      });

      //
      this.get_data();
    },

    //
    zixun(index) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
      }).then(() => {
        //
        this.get_data();

        //
        let arr = ["" + this.teachers[index].tel, "" + this.teachers[index].email];
        arr = arr.join('<br /><br />');
        //message
        this.$message({
          type: 'success',
          message: arr,
          duration: 15000,
          showClose: true,
          dangerouslyUseHTMLString: true
        });

        //
        this.file_post(index);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    },
  },
  created() {
    this.get_data();
  }
}
</script>

<style scoped>
.box-card{
    border: 0 solid transparent;
    background-color: transparent;
}

/**/
#radio_all {
  height: 15vh;
  position: relative;
}

/**/
#aside-box {
  width: 70px;
}

.el-card >>> .el-card__body{
    padding-left: 5px!important;
}

/**/
#button-box {
    margin: 0;
    padding: 0;
    text-align: left;
    width: 80vw;
    position: absolute;
    top: 40px;
    left: 90px;
}

/*main*/
.el-container >>> .el-main{
    padding: 0!important;
    /*padding: 20px 0!important;*/
}

#th-box {
    width: 100vw;
    overflow: hidden;
}

/**/
.box {
  background-color: white;
  margin-top: 10px;
  display: inline-block;
  width: 300px;
  height: 270px;
  border: 1px solid #EBEEF5;
  padding: 50px 0;
  text-align: center;
  transition: .3s;
  position: relative;
}

.box:hover {
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.3);
}

/**/
img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 1px solid #EBEEF5;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.2);
}

/**/
.teacher-intro {
  margin: 0 auto;
  width: 250px;
  text-align: left;
}

/* */
#question {
  position: absolute;
  top: 156px;
  right: 20px;
}

/* */
.zixun_button {
  position: relative;
  right: -45px;
}
</style>
