<script>
import axios from "axios";

export default {

  name:'FillVolunteer',
  data() {
    return {
      college_id: '',
      college_name: '',
      check_data:'',
      data_fenshu:'',
      data_paiming:'',
      showDialog:false,
      num:0,
      input:'',
      username:'',
      button_checked: 'ben',
      button_checked2:false,
      dialogVisible: false,
      dialogVisible2: true,
      value: false,
      num2: false,
      a: "0",
      checkList: [],//
      checkList1: ["", "", ""],//
      panduan: true,///
      add: "",
      //
       CX:{
         list1: '',
         list3: '',
         list4:'',
       },
      total: 0,
      pageSize: 10,  //
      pageSizes: [5, 10, 15, 20, 50],//
      currentPage: 1,  //
      pageCount: 7,   //
      info: [],
      info2:[],
      num_dialog: '',
      length_ben:'',
      length_zhuan:'',
      //
    }
  },
  methods: {
    major_show(majors,name,id){
      this.showDialog = true;
      this.info2=majors
      this.college_id=id
      this.college_name=name
    },
    Close_dialog(){
      this.$confirm(null,{
        message:'',
        confirmButtonText:'',
        cancelButtonText:'',
      }).then(()=>{
          this.showDialog = false;
      })
    },
    // 
    get1() {
      const loading = this.$loading({
        lock:true,
        text:'',
        spinner:'',
        background:'rgba(0, 0, 0, 0.7)',
      });

      if (this.CX.list1 === '') this.list1 = '';
      if (this.list4 === '') this.list4 = '';
      if (this.CX.list3 === '') this.list3 = '';

      axios({
        method:'POST',
        url:'api/students/recommend',
        data:{
          action:'rec_list_order',
          first:this.button_checked2===false?'':'',
          like:{
            sort_by:'',
            address:JSON.stringify(this.CX.list1),
            type:[this.CX.list3,this.CX.list4],
            major:[this.input],
            set:this.button_checked==='ben'?'':''
          },
          account:this.$cookies.get('user_name')
          // account:'12345678'
        }

      }).then((resp)=>{
        this.info = resp.data.college_major;
        this.total= resp.data.college_major.length
          console.log(this.info)
      }).catch((err)=>{
        console.log(err)
      })

      loading.close();
    },



    choose_major(val) {
      if (val == 'ben')
        return 1;
      else {
        return 2;
      }
    },

    // dialog
    show_dialog(val) {
      if (val == 1) {
        return true;
      } else {
        return false
      }
    },

    // 
    getadd(mName,mId) {
      axios({
        method:'POST',
        url:'api/students/gkorder',
        data:{
          action:'add_order',
          data:{
            college_name:this.college_name,
            college_id:this.college_id,
            major_name:mName,
            major_id:mId,
          },
          major_set:this.button_checked==='ben'?'':'',
          // account:'12345678'
          account:this.$cookies.get('user_name')
        }
      }).then((resp)=>{
        if(resp.data.ret===0){
          this.$message.success('')
          console.log(this.college_name)
        }else{
          this.$message.error(resp.data.msg)
        }
      })
    },

    close_dialog() {
      this.$cookies.set('number_dialog_school', 0, '0');
    },


  },

}
</script>

